import React, { useState } from 'react';
import app from './Firebase';
import Dropzone from 'react-dropzone';
import Progress from './Progress';
//import { render } from '@testing-library/react';
import Button from '@material-ui/core/Button'; 
import 'firebase/storage';
import 'firebase/auth';
import 'firebase/firestore'

 

const User = () => {
    const [State, setState] = useState({ 
        progress:0,
        user_id: 'Photos',
        isUploading: null,
    })
     
     

    const handleUpload = ({history}, files) => {
            const uploadTask = app.storage().ref(`image/${files.name}`).put(files);
            alert('file uploading');
        uploadTask.on("state_changed",
        (snapshot) => {
            const isUploading = true;
            setState({ isUploading });
            const progress = Math.round(( snapshot.bytesTransferred  / snapshot.totalBytes )  * 100 );
            setState({ progress });
        },
        (error) => {
            alert(error);
        },
        () => {
            app.storage().ref("image")
            .child(files.name)
            .getDownloadURL()
            .then((Url) => {
                const isUploading = false;
                setState({isUploading});
                const image={
                    url:Url,
                     
                }
            app.firestore().collection(State.user_id).add(image).then(res => {
                history.push("/photo");
            });
        });
        }

        )
        }
    
        return (
            <div className="container">
                
                  {State.isUploading ? (
                      <Progress percentage={State.progress} />
                  ) : ""}  
            
               <h1>upload a file</h1>
               <Dropzone onDrop={handleUpload}>
                   {({getRootProps, getInputProps}) => (
                     <section className='drop-zone'>  
                         <div {...getRootProps()}>
                             <input {...getInputProps()} />
                             <p>Drag 'n' drop some files here, or click to select files</p>
                        </div>
                     </section> 
                   )}
                   
               </Dropzone>
               <Button variant="contained" color="secondary" className="btn-logout" onClick={()=> app.auth().signOut()} >
                 Log out
               </Button>
            </div>
        )
     
}
export default User;